@import "./settings.scss";
@import "./typography.scss";

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  background: $color-light;
  font-family: $font-text;
  font-size: ($base-unit * 0.8);
  line-height: 1.7;
  color: $color-dark;
  letter-spacing: 0.5px;
  overflow-y: scroll;
}

a,
a:hover,
a:visited {
  text-decoration: none;
}
